import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { resultValue } from '../dataVal';

import { HomePage } from "../home/home";

@IonicPage()
@Component({
  selector: 'page-view',
  templateUrl: 'view.html',
})
export class ViewPage {

  private resultVal: resultValue;
  /** 벌고있는 돈 */
  private earning = {
    calc: 0,
    show: '0',
    module: '0',
  };

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    if (navParams.get('data') === undefined)
      navCtrl.setRoot(HomePage);
    else {
      this.resultVal = navParams.get('data');
      this.realtimeRefresh();
    }
  }
  /** 마지막 월급날(시작점) */
  private lastMonth: any;
  /** 이제 받게될 날 */
  private fastestPayday: any;
  ionViewDidLoad() {
    if (this.resultVal !== undefined) {
      this.resultVal.salary = Number(this.resultVal.salary);
      if (this.resultVal.payTerm == '연봉') {
        this.resultVal.salary /= 12;
      }
      this.resultVal.salary *= 10000;
      this.setCourse();
    }
  }
  /** 시작점, 끝점 파악하기 */
  setCourse() {
    let now = new Date().toLocaleDateString('ko');
    let sep = now.split('.');
    let sep2 = now.split('.');
    if (Number(sep[2]) < this.resultVal.payday) {
      sep[1] = ' ' + (Number(sep[1]) - 1 || 12).toString();
      sep[2] = ' ' + this.resultVal.payday;
      sep2[2] = ' ' + this.resultVal.payday;
    } else {
      sep[2] = ' ' + this.resultVal.payday;
      sep2[1] = ' ' + (Number(sep[1]) + 1 > 13 ? 1 : Number(sep[1]) + 1).toString();
      sep2[2] = ' ' + this.resultVal.payday;
    }
    this.lastMonth = new Date(sep.join());
    this.fastestPayday = new Date(sep2.join());
  }
  private refresh: any;
  /** 프레임처리 */
  realtimeRefresh() {
    this.refresh = setInterval(() => {
      let now = new Date().getTime();
      let day = new Date().getDate();
      if (day == this.resultVal.payday)
        this.setCourse();

      this.earning.calc = this.resultVal.salary * (now - this.lastMonth) / (this.fastestPayday - this.lastMonth);
      let tmp = Number(this.earning.calc.toFixed(2)).toLocaleString().split('.');
      if (tmp[1] !== undefined) { // 소수점 두자리 고정 틀
        if (tmp[1].length == 1)
          tmp[1] += '0';
      } else {
        tmp[1] = '00';
      }
      this.earning.show = tmp[0];
      this.earning.module = tmp[1];
    }, 73);
  }

  ionViewWillLeave() {
    clearInterval(this.refresh);
    if (this.resultVal !== undefined) {
      if (this.resultVal.payTerm == '연봉')
        this.resultVal.salary *= 12;
      this.resultVal.salary /= 10000;
      this.resultVal.salary = Math.round(this.resultVal.salary);
    }
  }

}



